@import '../../../../assets/scss/base';

$breadcrumb-divider: url(../../../../assets/icons/arrow_rht.svg);

.breadcrumb_nav {
    .breadcrumb {
        margin: 0;
        .breadcrumb-item {
            font-weight: $font-bold;
            &.active {
                font-weight: $font-regular;
            }
        }
    }
    .breadcrumb-item+.breadcrumb-item {
        padding-left: 20px;
    }
    .breadcrumb-item+.breadcrumb-item::before {
        content: "";
        background-image: $breadcrumb-divider;
        width: 20px;
        height: 26px;
        background-repeat: $no-repeat;
        background-position: $center $left;
    }
}
.divider {
    @include opacity (.10);
}

.tab_content {
    padding: 25px 0;
    .row {
        [class^="col-md-4"] {
            @media (min-width:1201px) {
                width: 33.33%;
            }
        }
        textarea {
            min-height: 50px;
            @media (min-width:1300px) {
                min-height: 60px;
            }
        }
    }
}

.profile_pic {
    @include flexbox;
    margin: 0 $auto 30px;
    max-width: 200px;
    position: $relative;
    .upload_file {
        background: $dark-green-color-900;
        @include border-radius (20px);
        @include flexCenter;
        padding: 5px;
        height: 36px;
        width: 36px;
        border: 0;
        position: $absolute;
        right: 20px;
        bottom: 20px;
        cursor: $pointer;
        img {
            width: 20px;
        }
    }
}