@import '../../assets/scss/base';

.job_btn {
  .btn {
    &:first-child {
      margin-left: 0 !important;
    }
    &:focus, &:active {
      border-color: $transparent!important;
    }
    &.selected {
      background-color: $primary-color;
      color: $white-color;
    }
  }
}

.label_title {
  display: $block;
  position: $relative;
  z-index: 1;
  span {
    font-size: $inherit;
    padding-right: 10px;
    background: $white-color;
  }
  &::after {
    content: '';
    background-color: $grey-color-700;
    height: 1px;
    width: 100%;
    position: $absolute;
    left: 0;
    top: calc(50% + 2px);
    z-index: -1;
  }
}

.job_btn {
  .btn {
    transition: $none;
    &::after {
      content: '';
      background: $primary-color;
      width: 20px;
      height: 20px;
      position: $absolute;
      left: calc(50% - 10px);
      bottom: -10px;
      @include transform (rotate(135deg));
      @include opacity(0);
      z-index: 1;
    }
    &.selected {
      &::after {
        @include opacity(1);
      }
      .checkbox {
        label {
          color: $white-color;
          &::before {
            background-color: $white-color;
          }
        }
      }
    }
  }
}